@import url(https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

p{
  margin: 0px;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App{
  color: #ffffff;
  font-family: 'Poppins', sans-serif !important;
}

.head{
  width: 100%;
  height: 45%;
  z-index: -1;
  position: fixed;
  background: linear-gradient(274.83deg, #5DB0DF 0.58%, #5D7ADF 74.3%);
}

@media screen and (min-width: 768px) {
  .App {
    margin: 0px 10%;
  }
}
.w-100{
  width: 100%;
}

.flex-grow-1{
  flex-grow: 1;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

* {
    box-sizing: border-box;
  }
  
  .nav {
    width: 100%;
    position: fixed;
    top: 0px;
    left: 0px;
    background: linear-gradient(274.83deg, #5DB0DF 0.58%, #5D7ADF 74.3%);
    z-index: 1;
    padding: 35px 10% 10px 10%;
    display: flex;
    align-items: center;
  }
  
  .nav > .nav-header {
    display: inline;
    float: left;
  }
  
  .nav > .nav-header > .nav-title {
    display: inline-block;
    font-size: 22px;
    color: #fff;
    padding: 10px 10px 10px 10px;
  }
  
  .nav > .nav-btn {
    display: none;
  }
  
  .nav > .nav-links {
    display: flex;
    align-items: center;
    float: right;
    font-size: 18px;
    width: 80%;
  }
  
  .nav > .nav-links > a {
    display: inline-block;
    padding: 13px 10px 13px 10px;
    text-decoration: none;
    color: #efefef;
    border-bottom: 2px solid transparent;
  }
  .ml-auto{
    margin-left: auto;
  }
  .nav > .nav-links > a:hover {
    border-bottom: 2px solid white;
  }
  
  .nav > #nav-check {
    display: none;
  }
  
  @media (max-width:768px) {
    .nav > .nav-btn {
      display: inline-block;
      position: absolute;
      right: 0px;
    }
    .nav > .nav-btn > label {
      display: inline-block;
      width: 50px;
      height: 50px;
      padding: 13px;
    }
    .nav > .nav-btn > label:hover,.nav  #nav-check:checked ~ .nav-btn > label {
      background-color: rgba(0, 0, 0, 0.3);
    }
    .nav > .nav-btn > label > span {
      display: block;
      width: 25px;
      height: 10px;
      border-top: 2px solid #eee;
    }
    .nav > .nav-links {
      position: absolute;
      display: block;
      width: 100%;
      background: linear-gradient(274.83deg, #5DB0DF 0.58%, #5D7ADF 74.3%);
      height: 0px;
      transition: all 0.3s ease-in;
      overflow-y: hidden;
      top: 85px;
      left: 0px;
    }
    .nav > .nav-links > a {
      display: block;
      width: 100%;
    }
    .nav > #nav-check:not(:checked) ~ .nav-links {
      height: 0px;
    }
    .nav > #nav-check:checked ~ .nav-links {
      height: calc(100vh - 10%);
      overflow-y: auto;
    }
  }

  .logo-icon{
      background: url(/sample-page/static/media/JobHunt.91fb9056.svg);
      background-size: contain;
      background-position: center;
      background-repeat: no-repeat;
      width: 18px;
      height: 18px;
      display: inline-block;
      margin-right: 10px  ;
  }

  .nav-title > p{
    display: inline;
  }
.home{
    position: relative;
    top: 100px;
}

@media screen and (max-width: 768px)  {
    .home{
        padding: 0 20px;
    }
}

.search-box{
    display: flex;
    align-items: center;
    padding:  20px 22px;
    box-shadow: 2px 12px 8px 2px rgba(0, 0, 0, 0.05);
    border-radius: 24px;
    background: #ffffff;
    justify-content: space-between;
}

.search-icon{
    background: url(/sample-page/static/media/search-icon.fdb69bca.svg);
    background-size: cover;
    background-position: center ;
    background-repeat: no-repeat;
    width: 27px;
    height: 27px;
    display: inline-block;
}
.search, .location-box{
    display: flex;
    align-items: center;

}
.search > input, .location-box > select{
    outline: none;
    border: none;
    font-size: 16px;
    line-height: 24px;
    color: #7b7b7b;
    font-family: 'Poppins';
    margin-left: 10px;
}
.location-box, .search-box{
    margin-right: 10px;
}
.location-icon{
    background: url(/sample-page/static/media/select_location.7a7a4e6f.svg);
    background-size: contain;
    background-position: center ;
    background-repeat: no-repeat;
    width: 27px;
    height: 27px;
    display: inline-block;
}
.search-btn{
    background: #F07987;
    border-radius: 8px;
    outline: none;
    border: none;
    padding: 12px 10px;
    color: #ffffff;
    font-family: 'Poppins';
    font-weight: 600;
    font-size: 24px;
    line-height: 36px;
}

.quote{
    margin: 5% 0 3% 0;
}

.quote p> strong{
    line-height: 72px;
    font-size: 48px;
}
.quote p{
    font-size: 20px;
    line-height: 30px;    
}
.business-card-container{
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    justify-content: space-between;
    margin-top: 35px;
    display: grid;
    grid-column-gap: 35px;
    -webkit-column-gap: 35px;
            column-gap: 35px;
    grid-row-gap: 35px;
    row-gap: 35px;
    grid-template-columns: 50% 50% ;
}
@media screen and (max-width: 768px) {
    .business-card-container {
        grid-template-columns: 100% !important;
    }
}

.card{
    padding: 25px;
    border-radius: 24px;
    background: #ffffff;
    box-shadow: 0px 4px 4px 2px rgba(0, 0, 0, 0.05);
    display: flex;
    align-items: center;
    color: black;
    max-width: 560px;
}
.icon-area{
    width: 150px;
    height: 150px;
    background: #C4C4C4;
    border-radius: 24px;
}
.card > .details{
    padding-left: 20px;
    width: 70%;
}
.card > .details > p > strong {
    font-weight: 500;
    font-size: 24px;
    line-height: 36px;
}
.card > .details > p {
    font-weight: 500;
    font-size: 14px;
    line-height: 21px;
}
.view-details-btn{
    color: #F07987;
    font-size: 14px;
    line-height: 21px;
    border: 2px solid #F07987;
    box-sizing: border-box;
    border-radius: 8px;
    width: 120px;
    height: 40px;
    background: #ffffff;
    float: right;
    margin-top: 10px;
}
